import styled from 'styled-components'
import React, { useState, useEffect } from 'react';
import Icon from '../Icon/Icon';
const Wrapper = styled.div`
    cursor: pointer;
    width:${props => props.width}px;
    height:${props => props.height}px;
    display:flex;
    .tab-title{
      position: relative;
    }
    .bet-new{
      width:30px;
      height:14px;
      border-radius:20px;
      font-size:10px;
      display: flex;
      background-color: #CD7A37;
      color:#E0ECFF;
      position: relative;
      top: -6px;
      align-items: center;
      text-transform: capitalize;
      justify-content: center;
    }
    .tab-left.inactive{
      border-radius:0px 0px 4px 0px;
    }
    .tab-right.inactive{
      border-radius:0px 0px 0px 4px;
    }
    .tab-left.active{
      border-radius:0px 4px 0px 0px;
      ${props => props.borderR};
    }
    .tab-right.active{
      border-radius:4px 0px 0px 0px;
      ${props => props.borderL};
    }
    .inactive,.active{
      width:50%;
      box-sizing: content-box;
      font-size:${props => props.size}px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        margin-right:${props => props.iconMarginRight}px
      }
    }
    .inactive{
      background:${props => props.inactiveBgColor};
      ${props => props.borderB}
    }
    .active {
      color: ${props => props.activeFontColor};
      background : ${props => props.activeBgColor};
      ${props => props.borderT};
    }
    .icon-tab{
      margin-right:20px;
      width:25px;
      height:25px;
    }
  `
export default function Tab({ tabSet, inactiveBgColor, iconMarginRight, activeBgColor, icon, className, width = 238, height = 47, size = 16, borderTop = false, borderBottom = false, divider = false, activeIndex = 0 }) {
  let arr = Object.keys(tabSet)
  const [selected, setSelected] = useState(arr[activeIndex])
  const [styles, setStyles] = useState()
  const [borderT, setBorderT] = useState()
  const [borderL, setBorderL] = useState()
  const [borderB, setBorderB] = useState()
  const [borderR, setBorderR] = useState()
  const [activeFontColor, setActiveFontColor] = useState()
  function select(item, e) {
    // e.stopPropagation()
    setSelected(item)
  }
  useEffect(() => {
    const arr = Object.keys(tabSet)
    setSelected(arr[activeIndex])
  }, [tabSet, activeIndex])


  useEffect(() => {
    const arr = Object.keys(tabSet)
    let checked = selected ? selected : arr[activeIndex]
    if (tabSet[checked]) {
      setStyles(tabSet[checked].style)
      let borderT = borderTop ? `border-top:1px solid ${tabSet[checked].borderColor}` : "border-top:0"
      let borderB = borderBottom ? `border-bottom:1px solid ${tabSet[checked].borderColor}` : "border-bottom:0"
      let borderL = divider ? `border-left:1px solid ${tabSet[checked].borderColor}` : "border-left:0"
      let borderR = divider ? `border-right:1px solid ${tabSet[checked].borderColor}` : "border-right:0"
      let activeFontColor = tabSet[checked].fontColor ? tabSet[checked].fontColor : '#FFFFFF'
      setBorderT(borderT)
      setBorderL(borderL)
      setBorderB(borderB)
      setBorderR(borderR)
      setActiveFontColor(activeFontColor)
    }

  }, [tabSet, selected])

  return (
    <div style={styles} className={className}>
      <Wrapper className='tab-header' iconMarginRight={iconMarginRight} width={width} height={height}
        activeFontColor={activeFontColor} activeBgColor={activeBgColor} inactiveBgColor={inactiveBgColor} size={size} borderT={borderT} borderB={borderB} borderL={borderL} borderR={borderR}>
        {arr.map((item, index) => (
          <div
            key={index}
            className={item === selected ? `tab-title ${tabSet[item].direction || tabSet[item].className} active` : `tab-title ${tabSet[item].direction || tabSet[item].className} inactive`}
            onClick={(e) => select(item, e)}>
            {tabSet[item].icon ? <Icon width={tabSet[item].iconWidth} height={tabSet[item].iconHeight} token={item === selected ? tabSet[item].icon : tabSet[item].inicon} /> : ""}  {item}
            {tabSet[item].isBet ? <>
              <span className='bet-new'>Beta</span>
            </> : null}
          </div>
        ))}
      </Wrapper>
      {tabSet[selected] ? tabSet[selected].content : ''}
    </div>
  )
}