import React, { useEffect, useState, useRef } from 'react';
import styled from "styled-components"
import searchLogo from '../../assets/img/search.svg'
const Wrapper = styled.div`
  .search-token{
  width: ${props => props.width }px;
  height: ${props => props.height}px;
  background: ${props => props.bgColor};
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:0 24px;
  border-radius:4px;
  img{
    width:18px;
    height:18px;
  }
  .search-input{
    border: 0;
    width:100%;
    height: 100%;
    background:transparent;
    outline-style: none;
    color: #FFFFFF;
    font-size: 18px;
    border-radius: 4px;
  }
}
`

export default function Search({ list, onChange,width='320',height='48', bgColor='rgba(85, 119, 253, 0.1)', searchName = 'bTokenSymbol',className ,placeholder = 'Search',onFocus,onBlur}) {
  const [search, setSearch] = useState('')
  const [length,setLength] = useState(0)
  const origin = useRef([])
  const searchChange = (e) => {
    let value = e.target.value
    setSearch(value)
  }

  useEffect(() => {
    if (search) {
      let value = search.toLocaleUpperCase()
      if (origin.current.length === 0) {
        origin.current = list
        setLength(list.length)
      }
      const arr = origin.current.filter(el => el[searchName] && el[searchName].toUpperCase().indexOf(value) >= 0)
      onChange(arr,value)
    } else {
      onChange(origin.current)
    }
  }, [search])
  useEffect(()=>{
    if (origin.current.length === 0) {
      setLength(list.length)
    }
  },[list])
  return (
    <Wrapper width={width} height={height} bgColor={bgColor} className={className}  >
      <div className='search-token'>
        <input
          type='text'
          className='search-input'
          placeholder={placeholder}
          value={search}
          onChange={(e) => { searchChange(e) }}
          // onFocus={e => onFocus && onFocus(e)}
          // onBlur={e => onBlur && onBlur(e)}
        />
        <img src={searchLogo} alt='' />
      </div>
    </Wrapper>

  )
}